body {
  font-family: sans-serif;
}

.js-list {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

.card {
  width: 30%;
  text-align: center;
  background-color: #c1c1c1;
  border-radius: 15px;
  padding: 20px;
}

.img {
  width: 100%;
  border-radius: 15px;
}

.article-link {
  text-transform: uppercase;
  color: #fff;
  background-color: rgba(45, 45, 233, .73);
  border-radius: 15px;
  padding: 10px 20px;
  text-decoration: none;
  display: inline-block;
}

.overview {
  height: 150px;
}

