body {
    font-family: sans-serif;
}

.js-list {
    margin-top: 10px;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 10px;
}

.card {
    width: 30%;
    text-align: center;
    border-radius: 15px;
    background-color: #c1c1c1;
    padding: 20px;
}

.img {
    width: 100%;
    border-radius: 15px;
}

.article-link {
    text-transform: uppercase;
    border-radius: 15px;
    display: inline-block;
    text-decoration: none;
    background-color: rgba(45, 45, 233, 0.731);
    color: white;
    padding: 10px 20px;
}

.overview {
    height: 150px;
}